import React from 'react'
import Testimonials from "../components/Testimonials";

const Statelevel = () => {
    return (
        <div className="pages-height m-5">
            <div className="text-center text-4xl font-bold underline">
                <p className="bg-blue-200 mb-5"> Major Activities And Awards:</p>
            </div>
            {/*<p>*/}
            {/*  <strong className="text-4xl ">Major Activities And Awards:</strong>*/}
            {/*</p>*/}
            <p>
                <strong>National Integration Camp (NIC):</strong>
            </p>
            <p>
                The National Integration Camp (NIC) is organized every year and the
                duration of each camp is of 7 days with day-night boarding &amp;
                lodging. These camps are held in different parts of the country. Each
                camp involves 200 NSS volunteers to undertake the scheduled activities.
            </p>

            <p>
                <strong>Objectives of the National Integration Camp</strong>
            </p>
            <p>Make the NSS volunteers aware of the following:</p>
            <ul>
                <li>Rich cultural diversity of India</li>
                <li>History of our diversified culture</li>
                <li>National pride through knowledge about India</li>
                <li>To integrate the nation through social service</li>
            </ul>
            <p>
                <strong>Adventure Program:</strong>
            </p>
            <p>
                The camps are held every year which are attended by approximately 1500
                NSS volunteers with at least 50% of the volunteers being girl students.
                These camps are conducted in Himalayan Region in the North and Arunachal
                Pradesh in North East region. The adventure activities undertaken in
                these camps includes trekking of mountains, water rafting, Para-sailing
                and basic skiing.
            </p>

            <p>
                <strong>Objectives of Adventure Program</strong>
            </p>
            <ul>
                <li>Promote various adventure activities among NSS volunteers</li>
                <li>Infuse the sense of love towards the various regions of India</li>
                <li>
                    Enhance leadership qualities, fraternity, team spirit and risk taking
                    capacity.
                </li>
                <li>Improvement of physical and mental strength</li>
                <li>Exposure to new vocational possibilities</li>
            </ul>
            <p>
                <strong>NSS Republic Day Parade Camp</strong>:
            </p>
            <p>
                The first Republic Day Camp of NSS Volunteers was held in 1988. The camp
                takes place in Delhi between 1st and 31st January every year with 200
                NSS selected volunteers who are good in discipline, March-past and
                cultural activities.
                <br />A Contingent of selected NSS volunteers participates in the
                Republic Day Parade at Rajpath, New Delhi on 26th of January every year
                in accordance with the guidelines and requisition of the Ministry of
                Defence.
            </p>
            <p>
                <strong>Objectives of NSS Republic Day Parade Camp</strong>
            </p>
            <ul>
                <li>
                    Enable the volunteers to interact with fellow members hailing from
                    various parts of India.
                </li>
                <li>
                    Experience the tradition, custom, culture, language of all states of
                    India.
                </li>
                <li>
                    Provide a chance to develop overall personality of the Student
                    volunteers.
                </li>
                <li>
                    Constitute the bond of patriotism, national integration, brotherhood
                    and communal harmony.
                </li>
            </ul>
            <p>
                <strong>National Youth Festivals</strong>
            </p>
            <p>
                National Youth Festivals are organized every year from 12<sup>th</sup>{" "}
                to 16<sup>th</sup> January by the Government of India, Ministry of Youth
                Affairs &amp; Sports in collaboration with the State Governments in
                different parts of the country. Eminent guests, speakers and youth icons
                are invited to address and interact with about 1500 participating NSS
                volunteers during the National Youth Festivals.
            </p>
            <p>
                <strong>Objectives of National Youth Festivals</strong>
            </p>
            <ul>
                <li>
                    Make the volunteers aware of the various festivals celebrated in the
                    country
                </li>
                <li>
                    Remind the volunteers of the cultural importance of festivals
                    celebrated in our country
                </li>
                <li>
                    Provide a chance to the NSS volunteers to interact with the resource
                    person/speaker/youth icons
                </li>
            </ul>
            <p>
                <strong>National Service Scheme Award</strong>
            </p>
            <p>
                The Ministry of Youth Affairs and Sports, Government of India had
                instituted the National Service Scheme Awards to recognize the voluntary
                service rendered by NSS volunteers, Programme Officers, N.S.S. units and
                the university/senior secondary council.
            </p>
            <p>
                These awards were instituted in the year 1993-1994. Since then, these
                awards are given away every year at various Levels. The details of these
                awards are as follows:-
            </p>
            <div>
                <p>
                    <strong>Objectives of National Service Scheme Award</strong>
                </p>
                <ul>
                    <li>
                        To recognize outstanding contribution by NSS student volunteers, NSS
                        Programme Officers and the Programme Coordinators in community
                        service
                    </li>
                </ul>
                <p style={{ marginLeft: "36pt" }}></p>
                <ul>
                    <li>
                        To encourage young NSS student volunteers to develop their
                        personality through community service.
                    </li>
                    <li>
                        To encourage the Programme Officers and the Programme Coordinators
                        of NSS for catering the needs of National Service Scheme through the
                        NSS volunteers.
                    </li>
                    <li>
                        To motivate NSS Volunteers for continuing their selfless service
                        towards community work.
                    </li>
                </ul>
            </div>
            <Testimonials />
        </div>
    )
}

export default Statelevel
